@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.home{
    height: 100vh;
    float: right;
    text-align: center;
    align-items: center;
    width: calc(100vw - 200px); 
}

.home_container{
    margin: 4.5rem;
}

.home > h1{
    margin-top: 2.5rem;
    font-size: 2rem;
}
.drop_box > span{
    font-size: 1rem;
    font-weight: bold;
}

.drop_zone{
    margin: 1rem  auto;
    position: relative;
    width: 700px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(222, 222, 222, 0.784);
    border-radius: 0.8rem;
    border: 2px dashed #000;
}

.drop_zone > input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.drop_zone > i{
    font-size: 7rem;
    margin-right: 1rem;
}

.drop_zone > span{
    font-size: 2rem;
}

.select_file > label{
    font-size: 1rem;
}

.hash_section{
    margin: 5rem auto;
}

.hash_box{
    margin: 0.8rem auto;
    position: relative;
    width: 800px;
    height: 80px;
    border: 2px solid rgb(142, 143, 143);
    border-radius: 1rem;
}

.hash_box > span{
    font-weight: bold;
}

.hash_box > h3{
    margin-top: 0.7rem;
}

.hash_box > button{
    cursor: pointer;
    font-size: 1rem;
    border: none;
    position: absolute;
    right: 7px;
    top: 31px;
}

.verify{
    height: 100%;
    float: right;
    text-align: center;
    align-items: center;
    width: calc(100vw - 200px); 
}

.verify_container{
    margin: 4.5rem;
}

.verify > h1{
    margin-top: 2.5rem;
    font-size: 2rem;
}
.verify_drop_box > span{
    font-size: 1rem;
    font-weight: bold;
}

.verify_drop_zone{
    margin: 1rem  auto;
    position: relative;
    width: 700px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(222, 222, 222, 0.784);
    border-radius: 0.8rem;
    border: 2px dashed #000;
}

.verify_drop_zone > input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.verify_drop_zone > i{
    font-size: 7rem;
    margin-right: 1rem;
}

.verify_drop_zone > span{
    font-size: 2rem;
}

.verify_select_file > label{
    font-size: 1rem;
}

.verify_hash_section{
    margin: 5rem auto;
}

.verify_hash_box{
    margin: 0.8rem auto;
    position: relative;
    width: 800px;
    height: 80px;
    border: 2px solid rgb(142, 143, 143);
    border-radius: 1rem;
}
.verified{
    border: 2px solid rgb(8, 225, 120);
    background: rgba(97, 230, 166, 0.445);
    color: rgb(35, 33, 38);
}

.unverified{
    border: 2px solid rgb(225, 41, 8);
    background: rgba(230, 97, 97, 0.445);
}

.verify_hash_box > span{
    font-weight: bold;
}

.verify_hash_box > h3{
    margin-top: 0.7rem;
}

.verify_hash_box > button{
    cursor: pointer;
    background: transparent;
    font-size: 1rem;
    border: none;
    position: absolute;
    right: 7px;
    top: 31px;
}

.tab_menu > h1{
    margin: 0.5rem auto;
    font-size: 3rem;
    width: -webkit-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: fit-content;
}

.tab_box{
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    height: 100vh;
    width: 200px;
    background: #ebeef4;
}

.tab_box > ul{
    margin: 5rem 0;
    padding-left: 0;
    grid-area: list;
    list-style: none;
}

.tab_box .tabs{
    margin: 2px;
    height: 3rem;
    width: 100%;
    background: #5d10a6d4;
    border: 1px solid #9530f4;
    text-align: center;
    line-height: 3rem;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.67);
    font-size: 1.2rem;
    border-radius: 0.3rem; 
}

.tab_box .active{
    color: #fff;
    background: #932ff0;
}


.profile{
    height: 100%;
    float: right;
    text-align: center;
    align-items: center;
    width: calc(100vw - 200px); 
}

.profile > h1{
    margin-top: 2.5rem;
    font-size: 2rem;
}

.profile_details{
    height: -webkit-fit-content;
    height: fit-content;
    width: calc(100vw - 400px); 
    margin: 4rem;
    border-radius: 0.7rem;
    border: 1px solid rgb(186, 186, 186);
    padding: 1rem;
    padding-left: 1.5rem;
    background: rgb(245, 231, 250);
}

.profile_details > span{
    margin: 1rem; 
}

.files{
    padding-left: 1rem;
}
.files > h2{
    font-size: 1.2rem;
}
.files > ul{
    list-style: decimal;
    text-align: start;
}

.files > ul > li{
    padding-left: 1rem;
}

.files > ul > li > span{
    float: right;
    padding-right: 10rem;
}
.app {
  width: 100vw;
  height: 100vh;
  background: #f0f2f5;
  font-family: 'Source Sans Pro', sans-serif;
  overflow-x: hidden;
}

.app > h1{
  margin: 3rem auto;
  font-size: 3rem;
}
